body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body::-webkit-scrollbar {
  width: 1em;
}
 
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
body::-webkit-scrollbar-thumb {
  background-color: #02083E;
  outline: 1px solid #02083E;
}
.App {
  text-align: center;
}

.squeeze{
    padding: 0;
    height: 100vh;
    -ms-scroll-snap-type: y mandatory;
        scroll-snap-type: y mandatory;
    overflow: scroll;
}

.homeNav{
    top: 0px;
    right: 0px;
    height : 100px;
    width: 50vw;
    z-index : 1;
    position : fixed;
    color : white;
    display : flex;
    justify-content : space-between;
}
.menuBtn{
    display: none;
}
.ourNav nav{
    position: fixed;
    left: 100px;
    top: 30px;
    font-size: 30px;
    color: white;
}


.homeNav ul{
    margin-top : 40px;
    display : flex;
}
.homeNav ul li{
    list-style : none;
    margin-right : 100px;
}
.homeNav ul li a{
    color : white;
    text-decoration : none;
}


@media (max-width : 800px) {
    .menuBtn{
        display: block;
    }
    .ourNav nav{
        left: 30px;
        top: 50px;
    }
   
    .homeNav {
        height: 100vh;
        width: 100vw;
        background-color: #02083E;
    }
    .homeNav ul{
        display: block;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        margin-top: 150px;
        margin-left: 100px;
        text-align: left;
    }
    .ourNav{
        z-index: 2;
        padding-top: 50px;
        padding-left: 50px;
        height: 10vh;
        position: fixed;
        font-size: 20px;
        color: white;
        width: 100vw;
        background-color: #02083E;
    }
    .homeNav ul li {
        font-size: 25px;
        width: 150px;
        margin-top: 10px;
    }
    .homeNav ul li:hover{
        color: yellow;
        cursor : pointer;
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
        transition: .5s;
    }
    .menuBtn{
        position: fixed;
        top: 50px;
        right: 20px;
        height: 30px;
        z-index: 3;
    }
}

.welcome{
    scroll-snap-align: start;
    height : 100vh;
    width : 100vw;
    background-color : #02083E;
    color : white;
    display : flex;
    justify-content : center;
    align-items : center;
    flex-wrap: nowrap;
    grid-gap: 2em;
    gap: 2em;
}
.welcome .textSection, .welcome .imageSection{
    width : 45vw; 
}

.welcome .imageSection{
    display: flex;
    justify-content: center;
    align-items: center;
    object-fit: fill;
}
.welcome .imageSection img{
    width: 100%;
}

.welcome .textSection p{
    color: rgba(255, 255, 255, 0.664);
    font-weight: bold;
}
.welcome .textSection h1{
    font-size : 40px;
}
.welcome .textSection a{
    margin-top: 20px;
    padding: 15px 0px;
    width: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #02083E;
    text-decoration: none;
    background-color: white;
    border-radius: 10px;
}
.welcome .textSection .get a{
    text-decoration: none;
    font-weight: bold;
}

@media (max-width : 800px) {
    .welcome .textSection b{
        font-size: 25px;
        margin-top: 40px;
    }
    .welcome .textSection p{
        font-size: 15px;
    }
    .welcome{
        flex-direction: column;
        padding-top: 120px;
        height: 100%;
    }
    .welcome .textSection,.welcome .imageSection{
        width: 80vw;
    }
    
}

.mission{
